








































































































import { Component, Vue } from 'vue-property-decorator';
import Regular from '../../utils/Regular';
import CommonFn from '../../utils/CommonFn';
import LoginApi from '../../api/login';
import Encryption from '../../utils/Encryption';

@Component({
  components: {},
})
export default class Login extends Vue {
  // 登录表单内容
  public signIn: any = {
    username: '',
    password: '',
  };
  // 注册表单内容
  public register: any = {
    name: '',
    username: '',
    password: '',
  };

  private LoginApi = new LoginApi();
  private Regular = new Regular();
  private Encryption = new Encryption();
  private CommonFn = new CommonFn(this);

  public clickSignIn(): void {
    if (this.chackUser(this.signIn)) {
      const psd = this.Encryption.encryptionMd5(this.Encryption.encryptionMd5(this.signIn.password));
      this.LoginApi.login({ ...this.signIn, password: psd })
      .then((res: any) => {
        if (res.status === 200) {
          this.signIn = { ...this.CommonFn.clearObjVal(this.signIn) };
          this.$store.commit('setToken', res.token);
          this.$store.commit('setUsername', res.username);
          localStorage.setItem('token', res.token);
          this.CommonFn.openNotificationWithIcon(
            'success',
            '登录成功',
            '欢迎回家',
          );
          this.$router.push('/');
        }
      });
    }
  }

  public clickRegister(): void {
    if (this.chackUser(this.register)) {
      const psd = this.Encryption.encryptionMd5(this.Encryption.encryptionMd5(this.register.password));
      this.LoginApi.register({ ...this.register, password: psd })
      .then((res: any) => {
        this.register = { ...this.CommonFn.clearObjVal(this.register) };
        this.CommonFn.openNotificationWithIcon(
          'success',
          '注册成功',
          '快去登录吧！！！',
        );
      });
    }
  }

  public chackUser(obj: any): boolean {
    if (!this.Regular.isEmailLegitimate(obj.username)) {
      this.CommonFn.openNotificationWithIcon(
        'error',
        '邮箱错误',
        '请输入正确邮箱格式',
      );
      return false;
    } else if (!this.Regular.isPasswordLegitimate(obj.password)) {
      this.CommonFn.openNotificationWithIcon(
        'error',
        '密码错误',
        '请输入包含大小写字母和数字的组合，不能使用特殊字符，长度在 8-10 之间)',
      );
      return false;
    } else {
      return true;
    }
  }
}
